@charset "UTF-8";
/**
 * Por convenção os nomes das cores devem começar com
 * dois traços (--), ao estilo das variáveis CSS.
 */
@import url("https://fonts.googleapis.com/css2?family=Arya:wght@300;400;500;700&display=swap");
.text---bg {
  color: rgb(242, 234, 230) !important;
}

.bg---bg {
  background-color: rgb(242, 234, 230) !important;
}

.text---primary {
  color: rgb(65, 83, 100) !important;
}

.bg---primary {
  background-color: rgb(65, 83, 100) !important;
}

.text---primary-o-75 {
  color: rgba(65, 83, 100, 0.75) !important;
}

.bg---primary-o-75 {
  background-color: rgba(65, 83, 100, 0.75) !important;
}

.text---secondary {
  color: rgb(243, 153, 112) !important;
}

.bg---secondary {
  background-color: rgb(243, 153, 112) !important;
}

.text---white {
  color: #fff !important;
}

.bg---white {
  background-color: #fff !important;
}

.text---black {
  color: #1b1b1b !important;
}

.bg---black {
  background-color: #1b1b1b !important;
}

* {
  font-family: "Arya", sans-serif;
}

h1 {
  font-size: calc(1vw + 1.5rem);
}

h2 {
  font-size: calc(1vw + 1.3rem);
}

h3 {
  font-size: calc(1vw + 1.2rem);
}

p {
  font-size: calc(1vw + 0.2rem);
}

@media (max-width: 1399.98px) {
  p {
    font-size: calc(1vw + 0.4rem);
  }
}
@media (max-width: 1199.98px) {
  p {
    font-size: calc(1vw + 0.6rem);
  }
}
@media (max-width: 991.98px) {
  p {
    font-size: calc(1vw + 0.8rem);
  }
}
@media (max-width: 767.98px) {
  p {
    font-size: calc(1vw + 0.9rem);
  }
}
@media (max-width: 575.98px) {
  p {
    font-size: calc(1vw + 1rem);
  }
  h1,
  h2,
  h3,
  h4,
  p {
    text-align: center;
  }
}
/**
 * Por convenção os nomes das cores devem começar com
 * dois traços (--), ao estilo das variáveis CSS.
 */
/**
 * Por convenção os nomes das cores devem começar com
 * dois traços (--), ao estilo das variáveis CSS.
 */
/**
 * General reset 
 */
html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
  /**
   * Custom scrollbar
   */
  /**
   * Custom selection color
   */
}
html::-webkit-scrollbar {
  width: 1.6rem;
}
html::-webkit-scrollbar-track {
  background-color: #fff;
}
html::-webkit-scrollbar-thumb {
  background-color: rgb(65, 83, 100);
  outline: none;
  border-radius: 0rem;
}
html ::selection {
  background-color: rgb(243, 153, 112);
  color: #fff;
}

html,
body {
  font-size: 62.5%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: rgb(242, 234, 230);
}

body {
  background: rgb(255, 229, 217);
  background: linear-gradient(90deg, rgb(255, 229, 217) 0%, rgb(242, 234, 230) 100%);
  font-size: 1.6rem;
}
body h3 {
  font-weight: 700;
  font-size: 96px;
}
body p,
body figure {
  margin: 0;
}
body main {
  flex: 1;
}

/**
 * Bootstrap reset
 */
.container {
  max-width: 1500px;
}

.tooltip {
  font-size: 16px;
}
.tooltip .tooltip-arrow::before {
  border-right-color: rgba(58, 58, 58, 0.9);
}
.tooltip .tooltip-inner {
  padding: 8px 16px;
  background-color: rgba(58, 58, 58, 0.9);
  border-radius: 4px;
}

img {
  -webkit-user-drag: none;
}
img.px {
  image-rendering: pixelated;
}
img.aa {
  image-rendering: -webkit-optimize-contrast;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: crisp-edges;
  image-rendering: optimize-contrast;
  -ms-interpolation-mode: nearest-neighbor;
}

a {
  text-decoration: none;
  color: rgb(65, 83, 100);
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

/* Animations */
@keyframes floating {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(15px);
  }
  100% {
    transform: translateY(0);
  }
}
/* Helpers */
.up {
  text-transform: uppercase;
}

.greyscale {
  filter: grayscale(100%);
}

[disabled]:hover {
  cursor: not-allowed;
}

/* Buttons */
button {
  all: unset;
}

.navbar {
  position: unset;
  display: unset;
  flex-wrap: unset;
  align-items: unset;
  justify-content: unset;
  padding-top: unset;
  padding-bottom: unset;
}

.form-control,
.btn {
  font-size: 1.6rem;
}

.btn {
  padding: 12px 16px;
}

.form-control:focus {
  box-shadow: none;
}

/**
 * Por convenção os nomes das cores devem começar com
 * dois traços (--), ao estilo das variáveis CSS.
 */
.btn {
  display: inline-block;
  border-radius: 24px;
  padding: 12px 16px;
  border: none;
  transition: 0.2s ease-in-out;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
}

/**
 * Solid button
 */
.btn {
  background-color: rgb(65, 83, 100);
  color: #fff;
  box-shadow: 2px 3px 8px rgba(0, 0, 0, 0.25);
}
.btn:hover {
  background-color: #4b6073;
  box-shadow: 2px 8px 16px rgba(0, 0, 0, 0.3);
}
.btn:active {
  background-color: #556d83;
}
.btn:focus-visible {
  background-color: #6c869f;
}

header .announcement {
  background-color: rgb(65, 83, 100);
  color: rgb(243, 153, 112);
  border-bottom: 1px solid lightgray;
  font-size: 15px;
}
header .announcement div {
  display: flex;
  justify-content: space-between;
}
header .announcement div ul {
  display: flex;
  align-content: center;
  gap: 1rem;
}
header .announcement div ul li {
  display: flex;
  align-items: center;
  padding: 5px;
  gap: 0.5rem;
}
header .announcement div ul li a {
  display: flex;
  color: rgb(243, 153, 112);
}
header nav {
  padding-top: 10px;
  display: flex;
  height: 90px;
}
header nav .nav__logo {
  display: flex;
  width: 33.333%;
}
header nav .nav__logo a img {
  width: 100%;
  height: 100%;
}
header nav .nav__menu {
  display: flex;
  justify-content: flex-end;
  width: 66.666%;
}
header nav .nav__menu ul {
  display: flex;
  align-content: center;
  gap: 5rem;
}
header nav .nav__menu ul li {
  padding: 5px;
  font-size: 28px;
  display: flex;
  align-items: center;
  padding: 5px;
  gap: 0.5rem;
  margin-bottom: 2px;
}
header nav .nav__menu ul li:hover {
  transform: scale(1.1);
  margin-bottom: 0;
  border-bottom: 2px solid rgb(243, 153, 112);
}
header nav .nav__menu ul li:hover a {
  transition: all 250ms;
  color: rgb(243, 153, 112);
}
header nav .nav__contact {
  display: flex;
  width: 33.333%;
  justify-content: flex-end;
}
header nav .nav__contact ul {
  display: flex;
  align-content: flex-end;
  gap: 1rem;
}
header nav .nav__contact ul li {
  display: flex;
  align-items: center;
  padding: 5px;
  gap: 0;
}
header nav .nav__contact ul li a {
  display: flex;
  color: rgb(65, 83, 100);
}
header nav .nav__contact ul li:hover {
  transform: scale(1.1);
  margin-bottom: 0;
}
header nav .nav__contact ul li:hover a {
  color: rgb(243, 153, 112);
  transition: all 250ms;
}

main {
  color: rgb(65, 83, 100);
}
main .hero {
  padding-top: 100px;
  text-align: center;
  height: calc(100vh - 192px);
  display: flex;
  flex-direction: column;
  align-items: center;
}
main .hero .hero__heading {
  font-weight: 700;
  font-size: 125px;
  text-transform: uppercase;
}
main .hero .hero__subheading {
  width: 33%;
  font-weight: 400;
  font-size: 28px;
  margin-top: 100px;
}
main .hero .hero__photos {
  display: flex;
  justify-content: space-between;
}
main .hero .hero__photos div {
  background-color: white;
  padding: 10px 10px 50px 10px;
  position: absolute;
  bottom: 25rem;
  box-shadow: 0 0.2rem 1.2rem rgba(0, 0, 0, 0.2);
}
main .hero .hero__photos div img {
  object-fit: cover;
  width: 300px;
  height: 250px;
}
main .hero .hero__photos div:nth-child(1) {
  left: 30rem;
  rotate: -15deg;
}
main .hero .hero__photos div:nth-child(2) {
  right: 30rem;
  rotate: 15deg;
}
main .marquee {
  overflow: hidden;
  position: absolute;
  left: 0;
  display: flex;
  align-items: center;
  background-color: rgb(65, 83, 100);
  color: rgb(242, 234, 230);
  width: 100%;
}
main .marquee .marquee__text {
  align-items: center;
  display: flex;
  animation: marquee 10s linear infinite;
}
main .marquee .marquee__text span {
  display: flex;
  align-items: baseline;
  width: 550px;
  padding: 36px 0px;
  gap: 50px;
}
main .marquee .marquee__text p,
main .marquee .marquee__text i {
  font-size: 40px;
  text-transform: uppercase;
}
main .recent_work {
  margin-top: 25rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
main .recent_work h3 {
  text-transform: uppercase;
}
main .recent_work .recent_work__grid {
  margin-top: 10rem;
  display: grid;
  width: 100%;
  justify-content: center;
  align-items: center;
  grid-gap: 5rem;
  grid-template-columns: 33% 33% 33%;
}
main .recent_work .recent_work__grid .recent_work__figure {
  position: relative;
  width: 100%;
}
main .recent_work .recent_work__grid .recent_work__figure img {
  object-fit: cover;
}
main .recent_work .recent_work__grid .recent_work__figure:nth-child(1) img, main .recent_work .recent_work__grid .recent_work__figure:nth-child(2) img, main .recent_work .recent_work__grid .recent_work__figure:nth-child(4) img, main .recent_work .recent_work__grid .recent_work__figure:nth-child(5) img {
  width: 100%;
  height: 400px;
}
main .recent_work .recent_work__grid .recent_work__figure:nth-child(3) img {
  width: 100%;
  height: 100%;
}
main .recent_work .recent_work__grid .recent_work__figure:nth-child(1) {
  grid-column: 1/2;
  grid-row: 1;
}
main .recent_work .recent_work__grid .recent_work__figure:nth-child(2) {
  grid-column: 1/2;
  grid-row: 2;
}
main .recent_work .recent_work__grid .recent_work__figure:nth-child(3) {
  grid-column: 2;
  grid-row: 1/3;
}
main .recent_work .recent_work__grid .recent_work__figure:nth-child(4) {
  grid-column: 3/4;
  grid-row: 1;
}
main .recent_work .recent_work__grid .recent_work__figure:nth-child(5) {
  grid-column: 3/4;
  grid-row: 2;
}
main .recent_work .recent_work__grid .recent_work__figure div {
  position: absolute;
  height: 80px;
  width: 100%;
  bottom: 0;
  display: flex;
  text-transform: uppercase;
}
main .recent_work .recent_work__grid .recent_work__figure div p, main .recent_work .recent_work__grid .recent_work__figure div a {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 2.5rem;
}
main .recent_work .recent_work__grid .recent_work__figure div p {
  flex: 8;
  background-color: rgba(65, 83, 100, 0.75);
  color: rgb(242, 234, 230);
  padding: 10px 40px;
}
main .recent_work .recent_work__grid .recent_work__figure div button {
  flex: 4;
  background-color: rgb(243, 153, 112);
  color: rgb(65, 83, 100);
}
main .recent_work .recent_work__grid .recent_work__figure div button a {
  width: 100%;
}
main .about {
  margin-top: 20rem;
}
main .about > div {
  display: flex;
}
main .about > div img {
  object-fit: cover;
  width: 650px;
  height: 650px;
}
main .about > div > div {
  padding: 32px 0 32px 144px;
}
main .about > div > div h3 {
  margin-bottom: 5rem;
  text-transform: uppercase;
}
main .about > div > div > div {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
main .about > div > div > div button {
  text-transform: uppercase;
  font-size: 20px;
  width: fit-content;
  padding: 15px 30px;
  border-radius: 8px;
  background-color: rgb(65, 83, 100);
  color: rgb(242, 234, 230);
  transition: all 250ms ease-in-out;
}
main .about > div > div > div button:hover {
  scale: 1.1;
}
main .services {
  margin-top: 15rem;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}
main .services h3 {
  text-transform: uppercase;
  margin-bottom: 10rem;
}
main .services .services__photos {
  width: 100%;
}
main .services figure {
  position: relative;
}
main .services figure div {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgb(242, 234, 230);
  font-size: 60px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}
main .services img {
  filter: brightness(50%);
  object-fit: cover;
  width: 100%;
  height: 450px;
}
main .services .row:nth-child(1) div {
  margin-bottom: 5rem;
}
main .services .row:nth-child(2) div:nth-child(1) {
  padding-right: 2.5rem;
}
main .services .row:nth-child(2) div:nth-child(2) {
  padding-left: 2.5rem;
}

@keyframes marquee {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(-100%, 0);
  }
}